@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@300;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@300;500;700&display=swap);
body{margin:0;display:flex;justify-content:center;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
form{min-width:280px;padding:1rem;width:20%;display:flex;flex-direction:column;align-items:center;justify-content:center;background-color:#f8f8f811;border-right:.5px solid #f3f1f5;box-shadow:rgba(50,50,93,.13) 0px 50px 100px -20px,rgba(0,0,0,.18) 0px 30px 60px -30px}.form-input{display:flex;flex-direction:column;align-items:center}.form-input input{width:100%;border:none;border-radius:10px;padding:.2rem .6rem;margin:.4rem;color:#191919}.form-input label{font-weight:300}.submit-btn{color:#600 !important;background-color:transparent;border:1px solid #600 !important}.submit-btn:hover{cursor:pointer;color:#fff6f6 !important;background-color:#600;border:1px solid #600 !important}.expenses-list{padding:1rem;width:100%}.expenses{display:flex;justify-content:center;align-items:center;flex-wrap:wrap}.expense-item{width:clamp(150px,10vw,200px);max-height:200px;outline:.5px solid #f8f8f8;padding:.6rem;margin:.6rem;border-radius:10px;color:#f8f8f8}.expense-item-price{text-align:right}.item-date{text-align:center}.day{font-weight:700}.item-info{display:flex;align-items:flex-end;justify-content:space-between;padding-top:1rem}.item-info h2{font-size:1rem;font-weight:700;text-align:left}.filter-control{text-align:center;color:#f8f8f8}.filter-control select{color:#f8f8f8;background-color:transparent;border:none;padding-left:.4rem;font-size:1rem}.filter-control select:hover{cursor:pointer}.filter-control option{color:#191919}.chart{padding:1rem;border-radius:12px;background-color:transparent;text-align:center;display:flex;justify-content:space-around;height:10rem}.chart-bar{height:100%;display:flex;flex-direction:column;align-items:center}.chart-bar-inner{height:100%;width:100%;border-radius:10px;background-color:#ff5f6d;overflow:hidden;display:flex;flex-direction:column;justify-content:flex-end}.chart-bar-fill{background-color:#8b001a;width:100%;transition:all .3s ease-out}.chart-bar-label{color:#f8f8f8;font-size:.6rem;text-align:center;padding-top:.4rem}#root{height:100vh;background-image:linear-gradient(to right bottom, #ff5f6d, #8b001a);width:100%;display:flex;justify-content:center;align-items:center}.container{background-image:linear-gradient(to left bottom, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.1));height:80%;width:80%;display:flex;border-radius:10px;overflow:hidden;box-shadow:rgba(50,50,93,.13) 0px 50px 100px -20px,rgba(0,0,0,.18) 0px 30px 60px -30px}*{font-family:"Work Sans",sans-serif;padding:0;margin:0}
